export default {
  props: ["reason","orderId","reload","changeVisibileModal","cancelUrl","flag","goodsId","type","goodsSpec"],
  data() { 
	  return {
		  chooseData: null,
		  detail: '',
		  pictures: [],
		  static:ut.static,
		  phone:''
	  }
  },
  methods: {
	  choose(data){
			this.chooseData = data;  
	  },
		addcomment() {
			let pictures='',hasPicture=2;
			this.pictures.forEach(item=>{
				hasPicture = 1
				if(pictures){
					pictures+=','+item
				}else{
					pictures+=item
				}
				
			})
			const userinf = wx.getStorageSync('userinf')
			ut.request({
				data: {
				  "account": userinf.nickname||userinf.name,
				  "content": this.detail,
				  "hasPicture": hasPicture,//是否有图 1有 2无
				  "photo": userinf.photo,
				  "pictures": pictures,
				  "type": this.type,//1商品2服务
				  "review":"2",//是否为追评 1是 2否
				  "proId": this.orderId//订单id
				},
				url: 'comment/add',
				c:true
			}).then(data=>{
				this.$parent.changeVisibileModal(false)
				this.$emit('reload');
				ut.totast("评价成功")
			})
		},
	  sub() {
			if(!this.detail){
				ut.totast("请输入评价")
				return;
			}
			this.addcomment();
	  },
	  uploadimg(){
	  	ut.uploadimg({
	  		callback:res=>{
	  			this.req_uploadimg(res);
	  			
	  		}
	  	})
	  },
	  req_uploadimg(res){
	  	const that=this;
	  	wx.uploadFile({
	  		url: ut.uploadimgurl+"common/imageUpload", //仅为示例，非真实的接口地址
	  		filePath: res,
	  		name: 'picture',
	  		header: {
	  			"Accept": "*/*"
	  		},
	  		success(res) {
	  			console.log(res)
	  			if(!res.data){
	  				ut.totast('上传图片失败')
	  				return;
	  			}
	  			res.data=JSON.parse(res.data);
	  			if(res.data.code==0){
	  				that.pictures.push(res.data.data);
	  			}else{
	  				ut.totast('上传图片失败')
	  			}
	  		}
	  	})
	  }
  }
}
