















import ut from '@/utils/index'
export default {
	props: ['type','text'],
	data() {
		return {
			provinceName:'',
			countyAreaName:'',
			cityName:'通州'
		}
	},
	methods: {
		bindRegionChange(e) {
			this.provinceName=e.target.value[0];
			this.countyAreaName=e.target.value[1];
			this.cityName=e.target.value[2];
		},
		goSearch(){
			if(this.type==1){
				ut.goto('../home/serch')
			}else{
				ut.goto('../build/serch')
			}
		},
		call(){
			ut.call();
		},
		totast(){
			ut.totast('现在只开通通州的服务，其他区域敬请期待！')
		}
	}
}
