









import ut from '@/utils/index'
export default {
	props: ['list','type'],
	data() {
		return {
			static: ut.static
		}
	},
	methods: {
		goServer(item){
			if(this.type==1){
				ut.goto(`../home/server?_id=${item.id}&title=${item.name}`)
			}else{
				ut.goto(`../build/build2?_id=${item.id}&name=${item.name}`)
			}
		}
	}
}
